import { Modal } from './modal';
export class OrderSuccessView {
    constructor() {
        this.modal = new Modal({
            templateId: 'success',
            onClose: () => this.handleClose(),
        });
    }
    // Рендеринг с данными заказа (только через шаблон)
    render(order) {
        const successTemplate = document.querySelector('#success');
        if (!successTemplate) {
            throw new Error('Template #success not found');
        }
        const successElement = document.importNode(successTemplate.content, true);
        const title = successElement.querySelector('.order-success__title');
        const description = successElement.querySelector('.order-success__description');
        const closeButton = successElement.querySelector('.order-success__close');
        if (title && description) {
            title.textContent = 'Заказ оформлен!';
            description.textContent = `Списано ${order.total} синапсов`;
        }
        if (closeButton) {
            closeButton.addEventListener('click', () => {
                this.modal.close();
            });
        }
        this.modal.setContent(successElement);
        this.modal.open();
    }
    // Получение экземпляра Modal
    getModal() {
        return this.modal;
    }
    // Закрытие модалки
    close() {
        this.modal.close();
    }
    // Уничтожение (если нужно)
    destroy() {
        this.modal.destroy();
    }
    // Обработка закрытия (опционально)
    handleClose() {
        // Здесь можно добавить логику при закрытии, если нужно
    }
}
