import { Modal } from './modal';
export class OrderContactsView {
    constructor() {
        this.modal = new Modal({
            templateId: 'contacts',
            onClose: () => this.handleClose(),
        });
        this.payButton = this.modal.getModalElement().querySelector('.button');
    }
    render() {
        this.modal.open();
    }
    setOnPayButtonClick(callback) {
        if (this.payButton) {
            this.payButton.addEventListener('click', (e) => {
                e.preventDefault();
                callback();
            });
        }
    }
    getModal() {
        return this.modal;
    }
    close() {
        this.modal.close();
    }
    destroy() {
        this.modal.destroy();
    }
    handleClose() {
    }
}
