import { Modal } from './modal';
export class OrderPaymentView {
    constructor() {
        this.modal = new Modal({
            templateId: 'order',
            onClose: () => this.handleClose(),
        });
        // Кэшируем кнопку "Далее" сразу
        this.nextButton = this.modal.getModalElement().querySelector('.order__button');
    }
    // Рендеринг (открытие модалки)
    render() {
        this.modal.open();
    }
    // Установка callback для кнопки "Далее"
    setOnNextButtonClick(callback) {
        if (this.nextButton) {
            this.nextButton.addEventListener('click', (e) => {
                e.preventDefault();
                callback();
            });
        }
    }
    getModal() {
        return this.modal;
    }
    close() {
        this.modal.close();
    }
    destroy() {
        this.modal.destroy();
    }
    handleClose() {
    }
}
